import {
  React,
  Component,
  Fragment,
  useCallback,
  useEffect,
  useContext,
  useReducer,
  useMemo,
  useRef,
  useState,
  createContext,
  useValue,
  Context,
  Box,
  View,
  Text,
  Button,
  Image,
  TextInput,
  ScrollView,
  Picker,
  Slider,
  FlatList,
  SectionList,
  Switch,
  ActivityIndicator,
  Alert,
  Modal,
  StatusBar,
  ImageBackground,
  TouchableOpacity,
  TouchableHighlight,
  TouchableWithoutFeedback,
  KeyboardAvoidingView,
  SafeAreaView,
  SearchBar,
  Animated,
  Easing,
} from "./components"
import ReactDOM from 'react-dom'
import { Preview, ErrorBoundary, Editor, EditorContext } from './components/Editor'
const scope = {
  render: rrender(),
  React,
  Component,
  Fragment,
  useEffect,
  useContext,
  useReducer,
  useMemo,
  useCallback,
  useRef,
  useState,
  createContext,
  useValue,
  Context,
  View,
  Text,
  Button,
  Image,
  TextInput,
  ScrollView,
  Picker,
  Slider,
  FlatList,
  SectionList,
  Switch,
  ActivityIndicator,
  Alert,
  Modal,
  StatusBar,
  ImageBackground,
  TouchableOpacity,
  TouchableHighlight,
  TouchableWithoutFeedback,
  KeyboardAvoidingView,
  SafeAreaView,
  SearchBar,
  Animated,
  Easing,
}

function rrender() {
  return function render(element) {
    ReactDOM.render(element, document.getElementById("preview"))
  }
}


const temCode = `
const App = () => <Text color="red">Hi Kids</Text>
`

const App = () => {
  const [current, setCurrent] = useState("New App")
  const [code, setCode] = useState(temCode.toString());
  const [isOpen, setIsOpen] = useState(false);
  const [isSave, setIsSave] = useState(true);

  const [state, setState] = useState({
    current: "New App",
    data: {
      "New App": temCode.toString()
    }
  })

  const clickItem = (item) => {
    setCurrent(item)
    setCode(state.data[item])
    setIsOpen(false)
    setIsSave(true)
  }

  const deleteItem = (item) => {
    if (current === item) {
      setCurrent("New App")
      state.current = "New App"
      setCode(temCode.toString())
      setIsSave(true)
    }
    delete state.data[item];
    setState({
      ...state,
      current: state.current,
      data: state.data
    })
    if (Object.keys(state.data).length === 1) setIsOpen(false)
    localStorage.setItem('@rnkids', JSON.stringify(state))
  }

  const saveData = () => {
    state.current = current;
    state.data[current] = code;
    setState({
      ...state,
      current: state.current,
      data: state.data
    })
    localStorage.setItem('@rnkids', JSON.stringify(state))
    setIsSave(true)
  }


  const newApp = () => {
    state.current = "New App"
    setState({
      ...state,
      current: state.current
    })
    setCurrent("New App")
    setCode(temCode.toString())
    setIsSave(true)
  }

  const toggleSidePanel = () => {
    setIsOpen(!isOpen)
  }

  const onUpdateFileName = text => {
    setCurrent(text)
    if (isSave) setIsSave(false)
  }

  const onChange = (ct) => {
    if (ct !== code) {
      setCode(ct)
      setIsSave(false)
    };
  }

  useEffect(() => {
    const reload = async () => {
      const _d = await JSON.parse(localStorage.getItem('@rnkids'));
      if (_d) {
        setState(_d)
        setCurrent(_d.current)
        setCode(_d.data[_d.current])
      }
    }
    reload();
  }, [])

  return (
    <EditorContext value={{ scope, onChange, code }}>
      <View flex={1} flexDirection="row">
        <View width={isOpen ? 200 : 0} transition="width 0.5s" bg="black">
          <Text color="white" fontWeight="bold" textAlign="center" height={44} mt={20}>Files</Text>
          <FlatList
            flex={1}
            data={Object.keys(state.data).filter(key => key !== "New App")}
            renderItem={({ item }) =>
              <View mt={8} flexDirection="row" justifyContent="space-between">
                <TouchableOpacity flex={1} onPress={() => clickItem(item)}>
                  <Text
                    bg="#2B2727"
                    color="white"
                    pl={8}
                    fonSize={24}
                    height={24}
                  >{item}</Text>
                </TouchableOpacity>
                <Button color="red" bg="#2B2727" size={24} onPress={() => deleteItem(item)}>❌</Button>
              </View>
            }
          />
        </View>
        <View flex={1}>
          <View flexDirection="row" width="100%" height={44} bg="black" justifyContent="flex-start" alignItems="center">
            <Button color="white" size={40} fontSize={20} ml={10}
              onPress={toggleSidePanel}
            >{isOpen ? "📁" : "📂"}</Button>
            <Button
              borderRadius={8}
              color="white"
              size={32}
              fontSize={20} ml={20} mr={10}
              disabled={isSave}
              onPress={saveData}
            >💾</Button>
            <TextInput
              color="white"
              value={current}
              placeholder={current}
              placeholderTextColor="lightgrey" bg="#2B2727"
              multiline={false}
              onChangeText={onUpdateFileName}
            />
            <Button color="white" size={40} fontSize={18} ml={20} mr={10}
              onPress={newApp}
            >✛</Button>
          </View>
          <Editor />
          <Box
            position="absolute"
            top={0} right={0} width={300} height={600}
            justifyContent="center"
            alignItems="center"
            bg="transparent"
            backgroundImage="url('https://raw.githubusercontent.com/koenbok/Framer/master/extras/DeviceResources/iphone-6-silver.png')"
            backgroundSize="100% 100%"
            backgroundRepeat="no-repeat"
          >
            <ErrorBoundary>
              <Preview />
            </ErrorBoundary>
          </Box>
        </View>
      </View>
    </EditorContext>
  );
}

export default App;

