import { React, Box, createContext, useContext, Component, useEffect } from './index'
import { ControlledEditor } from "@monaco-editor/react";
import { transform } from '@babel/standalone'

//Context 
const EditorCtx = createContext();
const EditorContext = ({ children, value }) => <EditorCtx.Provider value={value}>{children}</EditorCtx.Provider>
const useEditorValue = () => useContext(EditorCtx)

const usePreview = ({ code, scope }) => {
    const transformCode = code => {
        return transform(code, {
            presets: [['stage-0', { decoratorsLegacy: true }], 'react'],
        }).code
    }

    let sC = `
    const Root = () => <View bg="white" width={264} height={466} mt={67} ml={18.4} overflow="hidden"><App/></View>;
    render(<Root />);
    `
    useEffect(() => {
        let transformed = transformCode(code + sC)
        const func = new Function(...Object.keys(scope), transformed)
        func(...Object.values(scope))
    }, [code])
}

const Preview = () => {
    const { code, scope } = useEditorValue();
    usePreview({ code, scope })
    return <div id="preview" />
}

class ErrorBoundary extends Component {
    constructor(props) {
        super(props);
        this.state = { error: null, errorInfo: null };
        this.timeout = 0;
    }

    componentDidCatch(error, errorInfo) {
        // Catch errors in any components below and re-render with error message
        this.setState({
            error: error,
            errorInfo: errorInfo
        })
        this.timeout = setTimeout(() => {
            this.setState({
                error: null,
                errorInfo: null
            })
            if (this.timeout) clearTimeout(this.timeout);
        }, 2000);
    }

    render() {
        if (this.state.errorInfo) {
            // Error path
            return (
                <Box bg="white" width={264} height={466} mt={67} ml={18.4}>
                    <Box as={"h2"} color="red">Error</Box>
                    <Box color="darkblue">
                        {this.state.error ? this.state.error.message.toString() : null}
                    </Box>
                </Box>
            );
        }
        // Normally, just render children
        return this.props.children;
    }
}

const Editor = () => {
    const { code, onChange } = useEditorValue();

    let timeout = 0;

    const updateContent = (_, ct) => {
        let code = ct;
        if (timeout) clearTimeout(timeout);
        timeout = setTimeout(() => {
            //search function
            onChange(code)
        }, 1000);
    };

    return (
        <ControlledEditor
            height="95vh"
            theme="dark"
            value={code}
            onChange={updateContent}
            language="javascript"
            options={{
                fontSize: 24
            }}
        />
    );

}

export {
    Preview,
    ErrorBoundary,
    Editor,
    EditorContext,
    useEditorValue,
}